.page {
  width: 100%;
  overflow-x: hidden;
  font-family: Poppins, sans-serif;
  font-size: 32px;
  line-height: 32px;
  color: $main-text-color;
  scroll-behavior: smooth;

  &__body {
    width: 100%;
    overflow-x: hidden;
    margin: 0;
    min-width: 320px;
  }

  :has(.page__menu:target) {
    overflow: hidden;
  }

  &__menu {
    position: fixed;
    top: 0;
    right: 0;
    left: 0;

    opacity: 0;
    transition: all 0.3s;
    transform: translateX(-100%);
    pointer-events: none;

    &:target {
      opacity: 1;
      transform: translateX(0);
      pointer-events: all;
    }
  }
}
